import React, { FC } from 'react';
import styled from '@theme';
import { resizeTarget } from '@src/util';

const Container = styled.div<{ target: Typing.IReSizeTarget }>`
    position: relative;
    padding-bottom: ${(props) => resizeTarget(props.target)};
    overflow: hidden;
    .image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
`;

type IProps = {
    src: string;
    alt: string;
    target: Typing.IReSizeTarget;
};

const ImageForLinkPreview: FC<IProps> = ({ src, alt, target }) => {
    return (
        <Container target={target}>
            <img src={src} alt={alt} className="image" />
        </Container>
    );
};
export default ImageForLinkPreview;
